
/* ---------- BASE VARIABLES & IMPORTS ---------- */
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300;400;500;700&family=Rajdhani:wght@300;400;500;600;700&display=swap');

:root {
  /* Core Colors */
  --accent-red: #dc2626;
  --accent-red-dark: #991b1b;
  --accent-red-glow: #ef4444;
  --accent-red-bright: #ff003c;
  
  /* Background Colors */
  --bg-dark: #000000;
  --bg-darker: #0a0a0a;
  --bg-terminal: #0d0d0d;
  --bg-card: rgba(13, 13, 13, 0.75);
  
  /* Text Colors */
  --text-primary: #f3f4f6;
  --text-secondary: #d1d5db;
  --text-muted: #9ca3af;
  --text-highlight: #ffffff;
  
  /* Border Colors */
  --border-primary: rgba(220, 38, 38, 0.4);
  --border-secondary: rgba(220, 38, 38, 0.2);
  --border-glow: rgba(239, 68, 68, 0.6);
  
  /* Shadows */
  --shadow-red: 0 0 15px rgba(220, 38, 38, 0.3);
  --shadow-red-intense: 0 0 25px rgba(255, 0, 60, 0.5);
  --shadow-red-text: 0 0 5px rgba(255, 0, 60, 0.7);
  
  /* Gradients */
  --gradient-red: linear-gradient(135deg, var(--accent-red-dark) 0%, var(--accent-red) 100%);
  --gradient-red-glow: linear-gradient(135deg, rgba(153, 27, 27, 0.8) 0%, rgba(239, 68, 68, 0.8) 100%);
  --gradient-dark: linear-gradient(to bottom, rgba(0, 0, 0, 0.9) 0%, rgba(13, 13, 13, 0.95) 100%);
  
  /* Animation Speeds */
  --speed-slow: 3s;
  --speed-medium: 1.5s;
  --speed-fast: 0.5s;
  
  /* Dimensions */
  --navbar-height: 4rem;
  --section-spacing: 5rem;
  --card-border-radius: 0.5rem;
  --button-border-radius: 0.25rem;
  
  /* Z-indices */
  --z-background: -10;
  --z-content: 10;
  --z-overlay: 20;
  --z-navbar: 50;
  --z-modal: 100;
  --z-effects: 9000;
}

/* ---------- RESET & BASE STYLES ---------- */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: var(--accent-red) var(--bg-dark);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'JetBrains Mono', monospace, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-dark);
  color: var(--text-primary);
  overflow-x: hidden;
  line-height: 1.6;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Rajdhani', 'JetBrains Mono', sans-serif;
  font-weight: 700;
  letter-spacing: 0.5px;
  margin-bottom: 0.75rem;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s ease;
}

button, input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  border: none;
  outline: none;
  background: none;
}

button {
  cursor: pointer;
}

ul, ol {
  list-style-position: inside;
}

img {
  max-width: 100%;
  height: auto;
}

/* ---------- KEYFRAME ANIMATIONS ---------- */
@keyframes glitch {
  0% { 
    transform: translate(0);
    text-shadow: 0 0 0 var(--accent-red-glow);
  }
  10% { 
    transform: translate(-2px, 2px); 
    text-shadow: -1px 1px 0 var(--accent-red-glow);
  }
  20% { 
    transform: translate(-2px, -2px); 
    text-shadow: 1px -1px 0 var(--accent-red-glow);
  }
  30% { 
    transform: translate(2px, 2px); 
    text-shadow: -1px -1px 0 var(--accent-red-glow);
  }
  40% { 
    transform: translate(2px, -2px); 
    text-shadow: 1px 1px 0 var(--accent-red-glow);
  }
  50% { 
    transform: translate(-1px, 1px); 
    text-shadow: 1px -1px 0 var(--accent-red-glow);
  }
  60% { 
    transform: translate(1px, 1px); 
    text-shadow: -1px 1px 0 var(--accent-red-glow);
  }
  70% { 
    transform: translate(0); 
    text-shadow: 0 0 0 var(--accent-red-glow);
  }
  100% { 
    transform: translate(0); 
    text-shadow: 0 0 0 var(--accent-red-glow);
  }
}

@keyframes scanline {
  0% { 
    transform: translateY(-100%);
    opacity: 0.1;
  }
  50% {
    opacity: 0.2;
  }
  100% { 
    transform: translateY(100%);
    opacity: 0.1;
  }
}

@keyframes fast-scanline {
  0% { 
    transform: translateY(-100%) scaleY(0.1);
    opacity: 0.5;
  }
  50% {
    transform: translateY(50%) scaleY(0.5);
    opacity: 0.1;
  }
  100% { 
    transform: translateY(100%) scaleY(0.1);
    opacity: 0.5;
  }
}

@keyframes pulse-border {
  0% { 
    box-shadow: 0 0 0 0 rgba(220, 38, 38, 0.7); 
    border-color: rgba(220, 38, 38, 0.5);
  }
  50% { 
    box-shadow: 0 0 0 10px rgba(220, 38, 38, 0); 
    border-color: rgba(220, 38, 38, 0.9);
  }
  100% { 
    box-shadow: 0 0 0 0 rgba(220, 38, 38, 0); 
    border-color: rgba(220, 38, 38, 0.5);
  }
}

@keyframes pulse-glow {
  0% { 
    box-shadow: 0 0 5px 0 rgba(239, 68, 68, 0.5);
  }
  50% { 
    box-shadow: 0 0 20px 5px rgba(239, 68, 68, 0.8);
  }
  100% { 
    box-shadow: 0 0 5px 0 rgba(239, 68, 68, 0.5);
  }
}

@keyframes text-glow {
  0% { 
    text-shadow: 0 0 4px rgba(255, 0, 60, 0.5);
  }
  50% { 
    text-shadow: 0 0 10px rgba(255, 0, 60, 0.8), 0 0 20px rgba(255, 0, 60, 0.5);
  }
  100% { 
    text-shadow: 0 0 4px rgba(255, 0, 60, 0.5);
  }
}

@keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
    opacity: 1;
  }
  20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
    opacity: 0.33;
  }
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink {
  50% { border-color: transparent }
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes breathe {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes noise {
  0%, 100% { 
    background-position: 0 0; 
  }
  10% { 
    background-position: -5% -10%; 
  }
  20% { 
    background-position: -15% 5%; 
  }
  30% { 
    background-position: 7% -25%; 
  }
  40% { 
    background-position: 20% 25%; 
  }
  50% { 
    background-position: -25% 10%; 
  }
  60% { 
    background-position: 15% 5%; 
  }
  70% { 
    background-position: 0% 15%; 
  }
  80% { 
    background-position: 25% 35%; 
  }
  90% { 
    background-position: -10% 10%; 
  }
}

/* ---------- UTILITY CLASSES ---------- */
.animate-scanline {
  animation: scanline 8s linear infinite;
}

.animate-fast-scanline {
  animation: fast-scanline 2s linear infinite;
}

.hover-glitch:hover {
  animation: glitch 0.3s cubic-bezier(.25,.46,.45,.94) both infinite;
}

.animate-pulse-border {
  animation: pulse-border 3s infinite;
}

.animate-glow {
  animation: pulse-glow 3s infinite;
}

.animate-text-glow {
  animation: text-glow 3s infinite;
}

.animate-flicker {
  animation: flicker 2s infinite;
}

.animate-typing {
  overflow: hidden;
  white-space: nowrap;
  border-right: 0.15em solid var(--accent-red);
  margin: 0 auto;
  animation: 
    typing 3.5s steps(40, end),
    blink 0.75s step-end infinite;
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

.animate-rotate {
  animation: rotate 20s linear infinite;
}

.animate-breathe {
  animation: breathe 4s ease-in-out infinite;
}

.animate-fade-in-up {
  animation: fade-in-up 0.6s ease-out forwards;
}

.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 5s ease infinite;
}

.transition-all {
  transition: all 0.3s ease;
}

.transition-transform {
  transition: transform 0.3s ease;
}

.transition-colors {
  transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
}

.scale-on-hover:hover {
  transform: scale(1.05);
}

.translate-on-hover:hover {
  transform: translateY(-5px);
}

.rotate-on-hover:hover {
  transform: rotate(5deg);
}

.opacity-hover:hover {
  opacity: 0.8;
}

.delay-100 {
  animation-delay: 0.1s;
}

.delay-200 {
  animation-delay: 0.2s;
}

.delay-300 {
  animation-delay: 0.3s;
}

.delay-400 {
  animation-delay: 0.4s;
}

.delay-500 {
  animation-delay: 0.5s;
}

.delay-600 {
  animation-delay: 0.6s;
}

.delay-700 {
  animation-delay: 0.7s;
}

.delay-800 {
  animation-delay: 0.8s;
}

.delay-900 {
  animation-delay: 0.9s;
}

.delay-1000 {
  animation-delay: 1s;
}

